import Vue from 'vue'
import VueRouter from 'vue-router'
// component
const home = () => import('@/components/home');
const module = () => import('@/components/module');
const departements = () => import('@/components/departements');
const enseignants = () => import('@/components/enseignants');
const etduiants = () => import('@/components/etudiants');
const controles = () => import('@/components/controle');

Vue.use(VueRouter);

const routes = [
	{
		path: '/',
		name: 'home',
		component: home,
	},
	{
		path: '/modules',
		name: 'module',
		component: module,
	},
	{
		path: '/departements',
		name: 'departements',
		component: departements
	},
	{
		path: '/enseignants',
		name: 'enseignants',
		component: enseignants
	},
	{
		path: '/etudiants',
		name: 'etduiants',
		component: etduiants
	},
	{
		path: '/controles',
		name: 'controles',
		component: controles
	}
];

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
});

export default router
