import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const isProduction = process.env.NODE_ENV === 'production';

if (!isProduction) {
	console.log('DEV');
	// allow ajax cross
	$.ajaxSetup({
		crossDomain: true,
		xhrFields: {
			withCredentials: true
		}
	});
}


const state = {
	loading: false
};

const getters = {};

const mutations = {
	SET_LOADING(state, payload) {
		state.loading = payload
	}
};

const actions = {};

const store = new Vuex.Store({
	state,
	getters,
	mutations,
	actions
});

export default store;
